import React, { Component } from 'react';
import './App.css';
import Home from './components/home/home';
import InputCode from './components/inputCode/inputCode';
import firebase from './components/firebase/firebase';
import Confer from './components/giftList/giftList';


class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      showSplash: true,
      homeTitle: ''
    };
  }

  componentWillMount() {
    const db = firebase.database().ref().child('Views').child('home').child('homeTitle');
    db.on('value', (snapshot) => {
      this.setState({
        homeTitle: snapshot.val()
      });
    })
  }

  render() {
    return (
      <div className="App">
        {/*<Confer />*/}
        {this.state.homeTitle ? <InputCode /> : <Home homeTitle={'Yuli & Oscar'} />}
      </div>
    );
  }
}

export default App;
