import React, { Component } from 'react';
import './giftList.css';
import Gift from '../../components/giftList/gift';
import firebase from '../../components/firebase/firebase';
import flower from '../../flower.png';

class GiftList extends Component {

    constructor(props) {
        super(props);
        this.state = {
            gifts: [],
            modalText: 'Si deseas hacernos un regalo hemos hecho una lista de sugerencias que puedes tomar en cuenta para hacer más fácil tu búsqueda, también puedes escoger darnos tus muestras de cariño en efectivo. Pero recuerda que lo importante es que compartas con nosotros este día tan especial.',
            modalClose: false,
            selectedGifts: []
        };
        this.modalChangeHandler = this.modalChangeHandler.bind(this);
        this.onGiftSelected = this.onGiftSelected.bind(this);
        this.selectionCompleteHandeler = this.selectionCompleteHandeler.bind(this);
    }

    modalChangeHandler() {
        if (!this.state.modalClose) {
            this.setState({
                modalText: 'La elección que decidas dar será secreta para los novios.',
                modalClose: true
            });
        } else {
            const modal = document.getElementById('myModal');
            modal.style.display = "none";
        }
    }

    componentWillMount() {
        const db = firebase.database().ref().child('Gifts');
        db.on('value', (snapshot) => {
            this.setState({
                gifts: snapshot.val()
            });
        })
    }

    selectionCompleteHandeler(){
        let { selectedGifts } = this.state;
        const db = firebase.database().ref().child('Gifts');
        selectedGifts.forEach(g=>{
            db.child(g).remove();
        });
    }

    onGiftSelected(gift) {
        let { selectedGifts } = this.state;
        if (gift.checked) {
            selectedGifts.push(gift.value);
        } else {
            selectedGifts = selectedGifts.filter(g => g !== gift.value);
        }
        this.setState({ selectedGifts });
    }

    render() {
        console.log(this.state.selectedGifts);
        const gifts = this.state.gifts.map((v, i) => {
            return <Gift gift={{ name: v, id: i }} onGiftSelected={gift => this.onGiftSelected(gift)} />
        });
        return <div>
            <div>
                <div id='myHeader' className='gl-header'>
                    <button className='conf-button-back'><i className="fas fa-chevron-left "></i></button>
                    <div className='gl-list-title'>Lista de</div>
                    <div id='gl-gift' className='gl-gift-title'>Regalos</div>
                </div>
            </div>
            <div className="gl-content">
                {gifts}
            </div>
            <footer>
                <button className='gl-confirm-button' onClick={this.selectionCompleteHandeler}><i className="fas fa-check 2x"></i></button>
            </footer>

            {/*The Modal*/}
            <div id="myModal" className="modal">

                {/*Modal content*/}
                <div className="modal-content">
                    <div className="inner-border">
                        <img src={flower} alt='' className='gl-bottom-left' />
                        <img src={flower} alt='' className='gl-top-right' />
                        <p>{this.state.modalText}</p>
                        <button id='gl-modal-button' className='gl-confirm-button' onClick={this.modalChangeHandler}>{this.state.modalClose ? <i className="fas fa-times 2x"></i> : <i className="fas fa-chevron-down 2x"></i>}</button>
                    </div>
                </div>
            </div>
        </div>
    }
}
export default GiftList;