import React, { Component } from 'react';
import './confirmation.css';
import flower from '../../flower.png';
import Invitation from '../../components/invitation/invitation';
import firebase from '../../components/firebase/firebase';


class Confirmation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            previousComponent: null,
            family: props.family,
            verbiage: null,
            inputGests: null,
            code: props.code
        };
        this.backComponent = this.backComponent.bind(this);
        this.onInputChange = this.onInputChange.bind(this);
        this.sendConfirmation = this.sendConfirmation.bind(this);

    }

    componentWillMount() {
        const db = firebase.database().ref().child('Families').child('verbiage');
        db.once('value', (snapshot) => {
            this.setState({
                verbiage: snapshot.val()
            });
        });
    }

    onInputChange(event) {
        this.setState({ inputGests: event.target.value })
    }

    backComponent() {
        this.setState({ previousComponent: <Invitation family={this.state.family} code={this.state.code} /> });
    }

    sendConfirmation() {
        const db = firebase.database().ref().child('Families').child(this.state.code);
        db.update({
            "Gests": this.state.inputGests,
            "confirmed": true
        }).then(
            db.once('value', (snapshot) => {
                this.setState({
                    family: snapshot.val(),
                    previousComponent: <Invitation family={snapshot.val()} code={this.state.code} />
                });
            })
        );
    }

    render() {
        let firstParagraph = this.state.verbiage ? this.state.verbiage.p1.replace('_', this.state.family.Gests) : '';
        return (
            this.state.previousComponent ? this.state.previousComponent :
                <div id='conf-content'>
                    <button className='conf-button-back' onClick={this.backComponent}><i className="fas fa-chevron-left "></i></button>
                    <div className='conf-invitation conf-centered'>
                        <div className='conf-details conf-centered'>
                            <h1 className='conf-title'>{this.state.verbiage ? this.state.verbiage.title : ''}</h1>
                            <p className='conf-paragraph'>{firstParagraph}</p>
                            <br />
                            <p className='conf-paragraph'>{this.state.verbiage ? this.state.verbiage.p2 : ''}</p>
                            <div className='conf-input-container'>
                                <input className='conf-input-box' type='text' onChange={this.onInputChange} />
                            </div>
                            <img src={flower} alt='' className='conf-bottom-left' />
                            <img src={flower} alt='' className='conf-top-right' />
                        </div>
                    </div>
                    <button className='conf-button-send' onClick={this.sendConfirmation} ><i className="fab fa-telegram-plane"></i></button>
                </div>
        );
    }
}
export default Confirmation;