import React, { Component } from 'react';
import './invitation.css';
import flower from '../../flower.png';
import GiftList from '../../components/giftList/giftList';
import Confirmation from '../../components/confirmation/confirmation';

import firebase from '../../components/firebase/firebase';
import { dirname } from 'path';

class Invitation extends Component {

    constructor(props) {
        super(props);
        this.state = {
            code: props.code,
            family: props.family,
            showConfirmation: false,
            showGiftList: false
        };
        this.ShowConfirmationHandler = this.ShowConfirmationHandler.bind(this);
        this.ShowGiftListHandler = this.ShowGiftListHandler.bind(this);
    }

    ShowConfirmationHandler() {
        this.setState({ showConfirmation: true });
    }
    ShowGiftListHandler() {
        this.setState({ showGiftList: true });
    }

    render() {
        if (this.state.showGiftList) {
            return <GiftList />
        } else if (this.state.showConfirmation) {
            return <Confirmation family={this.state.family} code={this.state.code} />
        } else {
            return (
                <div id='inv-content'>
                    <div className='inv-background'>
                        <div className='inv-botton-left'>
                            <img src={flower} className='inv-flower-bottom-left' alt=''></img>
                        </div>
                        <div className='inv-body'>
                            <div className='inv-parent-lines'>
                                <div id='inv-line-right' className='inv-line'></div>
                                <div className='inv-line'></div>
                            </div>
                            <div className='inv-title'>
                                <span>{'Yuli & Oscar'}</span>
                            </div>
                            <div className='inv-parent-lines'>
                                <div id='inv-line-right' className='inv-line'></div>
                                <div className='inv-line'></div>
                            </div>
                            <div className='inv-verbiage'>
                                Con nuestro amor, con la presencia de Dios ante nosotros, y la bendición de nuestros padres, consagraremos nuestro amor en el Sacramento del Matrimonio.
                    </div>
                            <div className='inv-date'>
                                <div>16  <span className="inv-dot"></span>  02  <span className="inv-dot"></span>  2019</div>
                            </div>
                            <div className='inv-church'>Iglesia de Zarcero</div>
                            <div className='inv-hour'>2:00 pm</div>
                            <div className='inv-reception-title'>
                                Recepción
                    </div>
                            <div className='inv-reception-place'>
                                Zapote
                    </div>
                            <div className='inv-biblical-quote'>
                                "Nadie ha visto jamás a Dios, pero si nos amamos los unos a los otros, Dios permanece entre nosotros, y entre nosotros su amor se ha manifestado plenamente"
                    </div>
                            <div className='inv-biblical-ref'>
                                1 de Juan 4:12
                    </div>
                        </div>
                    </div>
                    <div className='inv-parent-lines'>
                        <button className={this.state.family.confirmed ? 'inv-button hidden' : 'inv-button'} disabled={this.state.family.confirmed} onClick={this.ShowConfirmationHandler}>Confirmar asistencia</button>
                        <button className='inv-button' onClick={this.ShowGiftListHandler}>Sugerencias de Regalos</button>
                    </div>
                </div>);
        }
    }
}
export default Invitation;