import React, { Component } from 'react';
import './giftList.css';

class Gift extends Component {
    constructor(props) {
        super(props);
        this.state = { gift: props.gift };
        this.GiftSelectedHandler = this.GiftSelectedHandler.bind(this);
    }

    GiftSelectedHandler(event) {
        let element = document.getElementById('gl-gift-' + this.state.gift.id);
        if (event.target.checked) {
            element.classList.add("gl-gift-checked");
        } else {
            element.classList.remove("gl-gift-checked");
        }
        console.log({value:event.target.value, checked:event.target.checked});
        this.props.onGiftSelected({value:event.target.value, checked:event.target.checked});
    }

    render() {
        return (
            <div id={'gl-gift-' + this.state.gift.id} className='gl-gift'>
                <div>
                    <label className="gl-container"><p>{this.state.gift.name}</p>
                        <input type="checkbox" onChange={this.GiftSelectedHandler} value={this.state.gift.id} />
                        <span className="checkmark"></span>
                    </label>
                </div>
            </div>
        );
    }

}
export default Gift;