import React, { Component } from 'react';
import './inputCode.css';
import flower from '../../flower.png';
import firebase from '../../components/firebase/firebase';
import Invitation from '../../components/invitation/invitation';



class InputCode extends Component {
    constructor(props) {
        super(props);
        this.state = {
            families: null,
            code: null
        };
        this.onClick = this.onClick.bind(this);
        this.onInputChange = this.onInputChange.bind(this);
    }

    onClick(e) {
        e.preventDefault();
        if (this.state.code) {
            const db = firebase.database().ref().child('Families').child(this.state.code);
            db.on('value', (snapshot) => {
                this.setState({
                    families: snapshot.val(),
                    invalidCode: snapshot.val() === null,
                    code: this.state.code
                });
            });
        } else {
            this.setState({
                families: null,
                invalidCode: true
            });
        }
    }

    onInputChange(event) {
        this.setState({ code: event.target.value })
    }

    render() {
        const msgError = <p className='msg-error'>Código inválido</p>
        if (!this.state.families) {
            return (
                <div className='ic-content'>
                    <img src={flower} alt='' className='ic-bottom-left' />
                    <div className = 'ic-title'><span>Digitar código</span></div>
                    <div className='ic-input-container'>
                        <input className='ic-input-box' type='text' onChange={this.onInputChange}/>
                    </div>
                    {this.state.invalidCode ? msgError : null}
                    <button className='ic-button' onClick={this.onClick}><span>Siguiente</span></button>
                </div>
            );
        } else {
            return (<Invitation family = {this.state.families} code={this.state.code}/>);
        }
    }
}
export default InputCode;