import React, { Component } from 'react';
import './home.css';
import flower from '../../flower.png';
import rings from '../../rings.png';

class HomeItem extends Component {
    constructor(props) {
        super(props);
        this.state = { homeTitle: props.homeTitle };
    }

    componentWillReceiveProps(nextProps) {
        this.setState({ homeTitle: nextProps.homeTitle });
    }

    render() {
        return (
            <div id='home-content' className='home-centered home-title'>
                <img src={rings} alt='' className='home-rings' /><br />
                <img src={flower} alt='' className='home-bottom-left' />
                <img src={flower} alt='' className='home-top-right' />
                <span>{this.state.homeTitle}</span>
            </div>
        );
    }
}
export default HomeItem;